import React, {Component} from "react";
import "./About.css";
import _ from "lodash";
import InfoBlock from "../InfoBlock/InfoBlock";
import avatar from "../Home/assets/avatar.png";
import Contacts from "../Contacts/Contacts";

class About extends Component {
    constructor(props) {
        super(props);

        this.state = {
            information: [
                {
                    id: 1,
                    title: '',
                    text: ['Профессия изначально должна быть актом любви. И никак не браком по расчету. И пока не поздно, не забывайте о том, что дело всей жизни — это не дело, а жизнь.']
                },
                {
                    id: 2,
                    title: 'Немного о скилах',
                    text: ['Верстаю адаптивно, семантично, кроссбраузерно. Знаю HTML5, CSS3, JavaScript, Pug, Gulp, Jquery, а так же Adobe Photoshop и Figma на уровне нарезки макетов.', 'С Redux я работал пока что в готовом его виде, но очень хочу поработать с ним поплотнее.', 'Так же имеется опыт создания 2D игр, с использованием Phaser 2.', 'Работаю по методологии БЭМ. Из препроцессоров использую Sass. По возможностям препроцессора использую переменные, вложенность, миксины.', 'Активно изучаю React + Redux', 'Поглядываю на Three.js и Vue ', 'Использую гит']
                },
                {
                    id: 3,
                    title: '',
                    text: ['Продолжаю изучать JS, практиковать современные подходы. Стараюсь не зацикливаться на каком-либо фреймворке, а делаю упор на JS, что бы изначально понимать как работает то, или иное внутри, будь то React или Vue и т.п.']
                },
                {
                    id: 4,
                    title: '',
                    text: ['С большим интересом смотрю на WebGL. Немного пробовал three.js']
                },
                {
                    id: 5,
                    title: 'Где я набираю опыт',
                    text: [ 'В настоящее время работаю фронтенд разработчиком в агенстве Traffic Isobar Digital Agency.']
                }
            ]
        };

    }

    render() {
        let infoBlocks = [];
        let stateInfo = this.state.information;

        _.each(stateInfo, (item) => {
            infoBlocks.push(
                <InfoBlock
                    key={item.id}
                    title={item.title}
                    text={item.text}
                />
            );
        });
        return(
            <div className="Home">
                <div className="title_box">
                    <img src={avatar} alt="avatar"/>
                    <h1 className="home_title">Рафаэль Гулиев</h1>
                </div>
                <div className="info_box">
                    <h2 className="info_box_title">Frontend разработчик</h2>
                </div>
                <div className="home_about">
                    {infoBlocks}
                </div>
                <div className="works">
                    <h2 className="works_title">Примеры работ</h2>
                    <a className="works_link" rel="noopener noreferrer" target="_blank" href="https://likeavenus.github.io/Leto/">Пример работы с анимациями</a>
                    <a className="works_link" rel="noopener noreferrer" target="_blank" href="https://likeavenus.github.io/first-p/">Школа сноуборда и горных лыж</a>
                </div>
                <Contacts/>
            </div>
        );
    }
}

export default About;